import { Injectable, TemplateRef } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class ToastService {
  toasts: any[] = [];

  // Push new Toasts to array with content and options
  show(textOrTpl: string | TemplateRef<any>, options: any = {}) {
    // Make sure toasts array doesnt grow bigger than 8
    if (this.toasts.length >= 8) {
      this.toasts.shift();
    }
    this.toasts.push({ textOrTpl, ...options });
  }

  // Callback method to remove Toast DOM element from view
  remove(toast) {
    this.toasts = this.toasts.filter((t) => t !== toast);
  }

  showSuccess(bodyText: string, delay: number = null) {
    const toastDelay = delay === null ? 7000 : delay;
    this.show(bodyText, {
      classname: 'bg-success text-light toast animated fadeIn',
      delay: toastDelay,
      autohide: true,
      headertext: 'Success',
    });
  }

  showError(bodyText: string, delay: number = null) {
    const toastDelay = delay === null ? 7000 : delay;
    this.show(bodyText, {
      classname: 'bg-danger text-light toast animated fadeIn',
      delay: toastDelay,
      autohide: true,
      headertext: 'Error',
    });
  }
}
