import { Injectable } from '@angular/core';
import {
  MatDialog,
  MatDialogRef,
  MAT_DIALOG_DATA,
} from '@angular/material/dialog';
import { Observable } from 'rxjs';
import { ConfirmDialogComponent } from './_confirm-dialog.component';
import { InformDialogComponent } from './_inform-dialog.component';
import { map, take } from 'rxjs/operators';

@Injectable()
export class DialogService {
  constructor(private dialog: MatDialog) {}

  confirmDialogRef: MatDialogRef<ConfirmDialogComponent>;
  informDialogRef: MatDialogRef<InformDialogComponent>;

  public OpenConfirm(options) {
    this.confirmDialogRef = this.dialog.open(ConfirmDialogComponent, {
      disableClose: true,
      data: {
        title: options.title,
        message: options.message,
        cancelText: options.cancelText,
        confirmText: options.confirmText,
      },
    });
  }
  public OpenInform(options) {
    this.informDialogRef = this.dialog.open(InformDialogComponent, {
      disableClose: true,
      data: {
        title: options.title,
        message: options.message,
        informText: options.informText,
      },
    });
  }

  public Confirmed(): Observable<any> {
    return this.confirmDialogRef.afterClosed().pipe(
      take(1),
      map((res) => {
        return res;
      })
    );
  }
  public Informed(): Observable<any> {
    return this.informDialogRef.afterClosed().pipe(
      take(1),
      map((res) => {
        return res;
      })
    );
  }
}
