import { ToastService } from './../_services/_toast/_toast.service';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { UserService } from '../_services';
import { UserRole } from '../enums';
import { Router } from '@angular/router';

@Component({
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
})
export class LoginComponent implements OnInit {
  constructor(
    private router: Router,
    private fb: FormBuilder,
    private userService: UserService,
    private toastService: ToastService
  ) {}

  loginForm: FormGroup;
  loginBox: any;

  ngOnInit() {
    this.initForm();
  }

  public async createAdminUser() {
    const user = {
      username: 'admin',
      userRole: UserRole.Admin,
      passwordHash: null,
    };
    const password = 'admin';
    try {
      await this.userService.CreateUser(user, password);
      this.toastService.showSuccess(
        `Admin user '${user.username}' created successfully`
      );
    } catch (error) {
      this.toastService.showError(error);
    }
  }

  public initForm() {
    this.loginForm = this.fb.group({
      username: [[], [Validators.required]],
      password: [[], [Validators.required]],
    });
  }

  get username() {
    return this.loginForm.get('username').value;
  }

  get password() {
    return this.loginForm.get('password').value;
  }

  public tryLogin() {
    if (this.loginForm.valid) {
      this.login();
    } else {
      this.toastService.showError('Missing required fields');
      this.loginForm.markAllAsTouched();
    }
  }

  public async login() {
    try {
      await this.userService.login(this.username, this.password);
      if (this.userService.currentUserValue.userRole === UserRole.User) {
        this.router.navigate(['/system']);
      } else if (
        this.userService.currentUserValue.userRole === UserRole.Admin
      ) {
        this.router.navigate(['/admin']);
      }
    } catch (error) {
      this.toastService.showError(error);
    }
  }
}
