/// <reference path='_db.service.d.ts' />
import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class DbService {
  public async getUsers(): Promise<any> {
    return await dbGetUsers();
  }

  public async getUser(id: number): Promise<any> {
    return await dbGetUser(id);
  }

  public async getUserByUsername(username: string): Promise<any> {
    return await dbGetUserByUsername(username);
  }

  public async addUser(user: any): Promise<any> {
    return await dbAddUser(user);
  }

  public async addAdminUser(user: any): Promise<any> {
    return await dbAddAdminUser(user);
  }

  public async updateUser(user: any): Promise<any> {
    return await dbUpdateUser(user);
  }

  public async deleteUser(id: number): Promise<any> {
    return await dbDeleteUser(id);
  }

  public async getBusinesses(): Promise<any> {
    return await dbGetBusinesses();
  }

  public async getBusiness(id: number): Promise<any> {
    return await dbGetBusiness(id);
  }

  public async getBusinessessByUserId(id: number): Promise<any> {
    return await dbGetBusinessessByUserId(id);
  }

  public async addBusiness(business: number): Promise<any> {
    return await dbAddBusiness(business);
  }

  public async updateBusiness(business: number): Promise<any> {
    return await dbUpdateBusiness(business);
  }

  public async deleteBusiness(id: number): Promise<any> {
    return await dbDeleteBusiness(id);
  }

  public async getCustomers(): Promise<any> {
    return await dbGetCustomers();
  }

  public async getCustomer(id: any): Promise<any> {
    return await dbGetCustomer(id);
  }

  public async addCustomer(customer: any): Promise<any> {
    try {
      return await dbAddCustomer(customer);
    } catch (error) {
      return Promise.reject(error);
    }
  }

  public async updateCustomer(customer: any): Promise<any> {
    return await dbUpdateCustomer(customer);
  }

  public async deleteCustomer(id: number): Promise<any> {
    return await dbDeleteCustomer(id);
  }

  public async getInvoices(): Promise<any> {
    return await dbGetInvoices();
  }

  public async getInvoice(id: number): Promise<any> {
    return await dbGetInvoice(id);
  }

  public async getInvoiceByInvoiceNumber(invoiceNumber: number): Promise<any> {
    return await dbGetInvoiceByInvoiceNumber(invoiceNumber);
  }

  public async addInvoice(invoice: any): Promise<any> {
    return await dbAddInvoice(invoice);
  }

  public async updateInvoice(invoice: any): Promise<any> {
    return await dbUpdateInvoice(invoice);
  }

  public async deleteInvoice(id: number): Promise<any> {
    return await dbDeleteInvoice(id);
  }

  public async getProducts(): Promise<any> {
    return await dbGetProducts();
  }

  public async getProduct(id: number): Promise<any> {
    return await dbGetProduct(id);
  }

  public async addProduct(product: any): Promise<any> {
    return await dbAddProduct(product);
  }

  public async updateProduct(product: any): Promise<any> {
    return await dbUpdateProduct(product);
  }

  public async deleteProduct(id: number): Promise<any> {
    return await dbDeleteProduct(id);
  }

  public async getProductTypes(): Promise<any> {
    return await dbGetProductTypes();
  }

  public async getProductType(id: number): Promise<any> {
    return await dbGetProductType(id);
  }

  public async addProductType(productType: any): Promise<any> {
    return await dbAddProductType(productType);
  }

  public async updateProductType(productType: any): Promise<any> {
    return await dbUpdateProductType(productType);
  }

  public async deleteProductType(id: number): Promise<any> {
    return await dbDeleteProductType(id);
  }

  public async getQuotations(): Promise<any> {
    return await dbGetQuotations();
  }

  public async getQuotation(id: number): Promise<any> {
    return await dbGetQuotation(id);
  }

  public async addQuotation(quotation: any): Promise<any> {
    return await dbAddQuotation(quotation);
  }

  public async updateQuotation(quotation: any): Promise<any> {
    return await dbUpdateQuotation(quotation);
  }

  public async deleteQuotation(id: number): Promise<any> {
    return await dbDeleteQuotation(id);
  }

  public async getStataments(): Promise<any> {
    return await dbGetStataments();
  }

  public async getStatement(id: number): Promise<any> {
    return await dbGetStatement(id);
  }

  public async addStatement(statement: any): Promise<any> {
    return await dbAddStatement(statement);
  }

  public async updateStatement(statement: any): Promise<any> {
    return await dbUpdateStatement(statement);
  }

  public async deleteStatement(id: number): Promise<any> {
    return await dbDeleteStatement(id);
  }
}
